.products_container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    background-color: var(--yellow);
    gap: 5%;
    padding: 5%;
    padding-bottom: 30%;
}

@media (max-width: 500px) {
    .products_container{
        display: flex;
        flex-direction: column;
        gap: 32px;
    }
} 